import { json, redirect } from "@remix-run/node";
import { useCatch, useLoaderData } from "@remix-run/react";
import type { ActionFunction, LoaderFunction } from "remix";
import { getParams } from "remix-params-helper";
import { z } from "zod";
import { supabaseClient } from "~/supabase";

export function headers() {
  return {
    "Cache-Control": "max-age=3600, s-maxage=4200",
  };
}

interface Category {
  id: number;
  name: string;
}

interface Subcategory {
  id: number;
  name: string;
  categories: Category;
}

interface BrandOwner {
  id: number;
  name: string;
}

interface Brand {
  id: number;
  name: string;
  companies: BrandOwner;
}

interface SubBrand {
  id: number;
  name: string;
  brands: Brand;
}

interface Product {
  id: number;
  name: string;
  sub_brands: SubBrand;
  subcategory: Subcategory;
}

interface Profile {
  id: string;
  username: string;
}

interface CheckIn {
  id: number;
  rating: number | null;
  review: string | null;
  profiles: Profile;
}

type LoaderData = { product: Product; checkIns: CheckIn[] | null };

const ParamsSchema = z.object({
  category: z.string(),
  subcategory: z.string(),
  brandOwner: z.string(),
  brand: z.string(),
  sub_brand: z.string(),
  product: z.string(),
});

export const action: ActionFunction = async ({ request, params }) => {};

export const loader: LoaderFunction = async ({ request, params }) => {
  const { success, data: decodedParams } = getParams(params, ParamsSchema);

  if (success) {
    const { data: product, error } = await supabaseClient
      .from("products")
      .select(
        "id, name, subcategories!inner(id, name, categories!inner(id, name)), sub_brands!inner(id, name, brands!inner(id, name, companies!inner(id, name)))"
      )
      .eq("name", decodedParams.product)
      .eq("sub_brands.name", decodedParams.sub_brand)
      .eq("sub_brands.brands.name", decodedParams.brand)
      .eq("sub_brands.brands.companies.name", decodedParams.brandOwner)
      .eq("subcategories.name", decodedParams.subcategory)
      .eq("subcategories.categories.name", decodedParams.category)
      .single();

    if (product === null) {
      throw Error("No product found!");
    }
    console.log("products: ", product);

    const { data: checkIns } = await supabaseClient
      .from("check_ins")
      .select("rating, review, product_id, profiles (id, username)")
      .eq("product_id", product!.id);
    console.log("checkIns: ", checkIns);

    return json<LoaderData>({ product, checkIns });
  }

  return redirect("/");
};

export default function Screen() {
  const { checkIns, product } = useLoaderData<LoaderData>();
  return (
    <>
      <h1>
        {product.sub_brands.brands.companies.name}
        {product.sub_brands.brands.name} {product.sub_brands.name}
        {product.name}
      </h1>

      {checkIns?.map((checkIn) => (
        <div key={checkIn.id}>
          {checkIn.profiles.username} {checkIn.rating}
        </div>
      ))}
    </>
  );
}

export function CatchBoundary() {
  const caught = useCatch();

  return (
    <div>
      <h1>
        {caught.status} {caught.statusText}
      </h1>
    </div>
  );
}

export function ErrorBoundary({ error }: { error: Error }) {
  return (
    <div>
      <pre>{error.message}</pre>
    </div>
  );
}
